
.card{position:relative;margin:5px;width:15em;box-sizing:padding-box;display:-ms-flexbox;display:flex;-ms-flex-flow:column;flex-flow:column;-ms-flex-align:stretch;align-items:stretch;border-radius:2px;box-shadow:2px 2px 6px black;background-color:#eee;background-size:contain;background-repeat:no-repeat;background-position:center center;font-size:0.8em;text-align:left
}
.card p{margin:0;padding:0
}
.card .content{display:-ms-flexbox;display:flex;-ms-flex-flow:column;flex-flow:column;-ms-flex-align:stretch;align-items:stretch;-ms-flex:1;flex:1;padding:0.3em 0.5em;background-color:rgba(240,240,240,0.9);background-clip:border-box;overflow:hidden
}
.card .content>*{-ms-flex:0;flex:0
}
.card .content .title{color:black;font-size:1.1em;line-height:1.3em;-ms-flex-item-align:start;align-self:flex-start
}
.card .content .fork{line-height:1em;font-size:0.6em
}
.card .content .fork:before{content:'\21B3 '
}
.card .content .desc{-ms-flex:1;flex:1;min-height:6em;font-size:0.85em;line-height:1.1em;padding:5px 0
}
.card .content .topics ul{list-style:none;margin:0;padding:0;display:-ms-flexbox;display:flex;font-size:0.85em
}
.card .content .topics ul .topic{margin:0 3px;padding:3px;border-radius:3px;line-height:1em;background-color:rgba(50,100,200,0.5);border:solid 1px #3264c8
}
.card .content .updated_at{font-size:0.7em
}



