
#content{z-index:10
}
#content .spacing{height:5vh
}
#content .region{position:relative;width:100%;min-height:60vh;padding:1.5vh 0px 3.5vh 0px;font-size:1em
}
#content .region header{font-size:2em
}
#content .region section{font-size:1.5em;min-height:80%
}
#content .region:nth-child(odd){background-color:#f8fceb
}
#content .region:nth-child(even){background-color:#f6f7d3
}



